.ContainerForm{
    max-width: 300px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
}

.FormLogin{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.textInput{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.3;
    letter-spacing: 0.03em;

    color: #212121;
    margin-bottom: 8px;
}

.LoginInput{
    outline: none;
        display: block;
        background: #282828;
        width: 240px;
        border: 0;
        border-radius: 4px;
        box-sizing: border-box;
        padding: 12px 20px;
        color: #f4f1f1;
        font-family: inherit;
        font-size: inherit;
        font-weight: 500;
        line-height: inherit;
        transition: 0.3s ease;
}

.btnLogin{
    outline: none;
    background: #6d6b6b;
    width: 140px;
    border: 0;
    border-radius: 4px;
    padding: 12px;
    color: #fff;
    font-family: inherit;
    font-size: 16px;
    line-height: 1.3;
    font-weight: 700;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}

@media (min-width: 520px) {
    .FormLogin {
        max-width: 500px;
    }
}